import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
      message:{},
      navIndex:0,
      isUpdate:false,
  },
  mutations: {
      update(state,val){
        for(let i in val){
            state.message[i]=val[i];
        }
      }
  },
  actions: {

  }
})
